
.filter-bar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  left: 0;
  bottom: 0;
  height: 36px;
  line-height: 36px;
  background-color: #fcfcfc;
  width: 100%;
  text-align: center;
  z-index: 1;
}
.filter-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  border-left: 1px solid #d2d2d2;
  border-top: 1px solid #d2d2d2;
}
.filter-item:first-child {
  border-left: 0;
}

.filter-layer {
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.filter-layer .panel{
  background-color: #fff;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 36px;
}
.filter-layer h3{
  margin: 0;
  font-size: 16px;
  padding: .5em 1em;
}
.filter-layer .no{}
.filter-layer .ok{
  float: right;
  color: #00b358;
}

.filter-layer ul {}
.filter-layer ul li {
  position: relative;
  line-height: 44px;
  border-top: 1px solid #e5e5e5;
  padding: 0 1em;
}
.filter-layer ul li.active {
  color: #00b358;
}
.filter-layer ul li.active.active::after {
  content: "";
  width: 14px;
  height: 6px;
  border-bottom: 2px solid #00b358;
  border-left: 2px solid #00b358;
  position: absolute;
  top: 50%;
  right: 8px;
  margin-top: -4px;
  -webkit-transform: rotate(-45deg) translateY(-50%);
  transform: rotate(-45deg) translateY(-50%);
}



